@charset "UTF-8";
html {
  box-sizing: border-box;
}
*,
*:before,
*:after {
  box-sizing: inherit;
}
.sui-layout {
  width: 100%;
  display: flex;
  flex-direction: column;
}
/**
 * Header / Searchbox
 */
.sui-layout-header {
  padding: 32px 24px;
  border-bottom: 1px solid #eeeeee;
}
/**
 * Body
 */
.sui-layout-body {
  background: #fcfcfc;
}
.sui-layout-body:after {
  content: "";
  height: 80px;
  width: 100%;
  display: block;
  position: relative;
  background: linear-gradient(to bottom, #fcfcfc 0%, #ffffff 100%);
  filter: progid:DXImageTransform.Microsoft.gradient( startColorstr="#fcfcfc", endColorstr="#ffffff",GradientType=0 );
}
@media (max-width: 800px) {
  .sui-layout-body:after {
    display: none;
  }
}
.sui-layout-body__inner {
  max-width: 1300px;
  margin-left: auto;
  margin-right: auto;
  display: flex;
  padding: 0 24px;
}
@media (max-width: 800px) {
  .sui-layout-body__inner {
    display: block;
    padding: 0 15px;
  }
}
/**
 * Sidebar / Filters
 */
@-webkit-keyframes fadein {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}
@keyframes fadein {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}
.sui-layout-sidebar {
  width: 24%;
  padding: 32px 32px 0 0;
}
@media (max-width: 975px) {
  .sui-layout-sidebar {
    padding-right: 0;
  }
}
@media (max-width: 800px) {
  .sui-layout-sidebar {
    z-index: 99;
    display: none;
    position: fixed;
    top: 0;
    left: 0;
    height: 100%;
    width: 100%;
    margin: 0;
    padding: 0 15px 30px 15px;
    background-color: #fcfcfc;
    overflow: auto;
  }
}
.sui-layout-sidebar--toggled {
  display: block;
  -webkit-animation: fadein 0.2s ease-out;
          animation: fadein 0.2s ease-out;
}
@media (max-width: 800px) {
  .sui-layout-sidebar-toggle {
    display: block;
    width: 100%;
    margin-top: 20px;
    padding: 10px;
    font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Helvetica, Arial, sans-serif;
    line-height: 1.5;
    font-weight: 400;
    font-size: 14px;
    font-weight: 700;
    color: #3a56e4;
    background-color: #ffffff;
    border: 1px solid #3a56e4;
    border-radius: 4px;
  }
  .sui-layout-sidebar .sui-layout-sidebar-toggle {
    margin-bottom: 20px;
  }
}
/**
 * Main / Results
 */
